import React from "react";
import PropTypes from "prop-types";
import $ from "jquery";

import OraModal from "../../../modules/core/components/ui/Modal";
import {OraLoader} from "../../../components/common/OraLoader";

FullScreenLoader.propTypes = {
    showLoader: PropTypes.bool,
    message: PropTypes.string,
    messageType: PropTypes.string,
    id: PropTypes.string.isRequired,
    parentId: PropTypes.string,
    showClose: PropTypes.bool,
    onClose: PropTypes.func
};

function FullScreenLoader(props = {showLoader: true, onClose: () => null, showClose: true}) {
    if(props.showLoader){
        $("#" + props.id + "Section").show();
    } else {
        $("#" + props.id + "Section").hide();
    }
    return (
        <OraModal
            size="narrow"
            escapeClose={false}
            clickClose={false}
            showClose={props.showClose}
            open={props.showLoader}
            onClose={props.onClose}
            id={props.id + "Modal"}
        >
            <div className="l-full-row">
                <div className="l-page-row">
                    <div className="l-row u-padding-s">
                        <div className="l-col l-col-small-1 l-col-medium-1 l-col-1 u-spacing-top-m u-position-relative">
                             <div className="u-vertical-middle">
                                <OraLoader
                                    loading={props.showLoader}
                                    id={props.id}
                                    parentId={props.parentId | props.id + "Parent"}
                                    size="s"
                                    />
                            </div>
                        </div>
                        <div id={props.id + "Section"} className="l-col l-col-small-11 l-col-medium-11 l-col-11">
                            <div className="u-font-bold u-padding-top u-vertical-middle">{props.message}</div>
                        </div>
                    </div>
                </div>
            </div>
        </OraModal>
    );
}

export default FullScreenLoader;